body {
    background-color: rgb(124, 113, 113);
}
.Card-input {
   
    margin-top: 30px;
    margin-right: 2px;
    margin-left: 35px;
}
.heading {
    margin-left: 35px;
    color: white;
}
.Card-div {
    box-shadow: 0 0 10px #666660;
    max-width: 288px;
    border-radius: 10px;
    margin: 20px;
    text-align: center;
    background-color: rgb(25, 25, 134);
    color: white;
}
.Card-img {
    max-width: 90%;
    margin: 15px;
}
.Card-body {
    font-size: 20px;
    font-family: Courier, monospace,Arial;
    padding-bottom: 15px;
}